* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timepicker {
  color: gray;
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-color: #ddd gray;
  padding: 4px;
  font-size: 14px;
}

.startbtn {
  color: #404040;
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid #404040;
  padding: 4px 8px;
  font-size: 14px;
}

.startbtn:disabled {
  color: gray;
  border: 1px solid gray;
}

::-webkit-calendar-picker-indicator {
  cursor: pointer;
  background: #f0f0f0;
}

.timer {
  gap: 10px;
  padding-top: 10px;
  display: flex;
}

.value, .label {
  text-align: center;
  display: block;
}

.value {
  font-size: 36px;
}

.label {
  text-transform: uppercase;
  font-size: 14px;
}

.alert {
  color: #fff;
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  background-color: #f88;
  border: 1px solid #800;
  border-radius: 4px;
  padding: 20px 10px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 10px;
  right: 10px;
}

.alert.js-hidden {
  opacity: 0;
  visibility: hidden;
}

